interface LevelUpCardProps {
  level: number;
  progressPercentage: number;
}

const LevelUpCard = ({ level, progressPercentage }: LevelUpCardProps) => (
  <div className="bg-white rounded-xl p-6 shadow-lg">
    <div className="flex flex-col items-center">
      <Leaf className="w-16 h-16 text-green-500 mb-4" />
      <h2 className="text-xl font-bold text-gray-800 mb-2">Level {level}</h2>
      <p className="text-sm text-gray-600 text-center mb-6">
        You've planted the first seed - growth begins here
      </p>
      <div className="w-full bg-gray-200 rounded-full h-2.5 mb-4">
        <div 
          className="bg-green-500 h-2.5 rounded-full" 
          style={{ width: `${progressPercentage}%` }}
        ></div>
      </div>
      <p className="text-xs text-gray-500">{progressPercentage}% to next level</p>
    </div>
  </div>
);

const PlantProgressCarousel = () => {
  const { level, progressPercentage } = usePlant();
  const [currentPage, setCurrentPage] = useState(0);
  
  const pages = [
    <GeneralRulesCard key="rules" />,
    <LevelUpCard key="level" level={level} progressPercentage={progressPercentage} />
  ];

  const nextPage = () => {
    setCurrentPage((prev) => (prev + 1) % pages.length);
  };

  const prevPage = () => {
    setCurrentPage((prev) => (prev - 1 + pages.length) % pages.length);
  };

  return (
    <div className="relative w-full max-w-md mx-auto overflow-hidden">
      <div className="relative h-96">
        <AnimatePresence mode="wait">
          <motion.div
            key={currentPage}
            initial={{ opacity: 0, x: 100 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -100 }}
            transition={{ duration: 0.3 }}
            className="absolute w-full"
          >
            {pages[currentPage]}
          </motion.div>
        </AnimatePresence>
      </div>
      
      {/* Navigation Arrows */}
      <button
        onClick={prevPage}
        className="absolute left-2 top-1/2 -translate-y-1/2 bg-white p-2 rounded-full shadow-md"
      >
        <ArrowLeft className="w-5 h-5 text-gray-600" />
      </button>
      <button
        onClick={nextPage}
        className="absolute right-2 top-1/2 -translate-y-1/2 bg-white p-2 rounded-full shadow-md"
      >
        <ArrowRight className="w-5 h-5 text-gray-600" />
      </button>
      
      {/* Pagination Dots */}
      <div className="flex justify-center mt-4 space-x-2">
        {pages.map((_, index) => (
          <button
            key={index}
            onClick={() => setCurrentPage(index)}
            className={`w-2 h-2 rounded-full ${
              currentPage === index ? 'bg-green-500' : 'bg-gray-300'
            }`}
            aria-label={`Go to page ${index + 1}`}
          />
        ))}
      </div>
    </div>
  );
};

const PlantPage = () => {
  const { level, currentStage, progressPercentage } = usePlant();
  
  return (
    <div className="min-h-screen bg-gradient-to-b from-blue-50 to-green-50 pb-20">
      <header className="bg-white shadow-sm p-4 sticky top-0 z-10">
        <h1 className="text-xl font-bold text-center text-gray-800">My Growth</h1>
      </header>

      <main className="container mx-auto px-4 py-8">
        <PlantProgressCarousel />
        <div className="mt-6">
          <Plant />
        </div>
        <SkillCompletion />
      </main>
    </div>
  );
};

export default PlantPage;